import React from "react"
import styled from "styled-components"
import { Link } from "gatsby"
import PostMetaItem from "../components/post-meta-item"

const ContentWrapper = styled.div`
  margin-bottom: 0.5rem;
  padding: 1rem;
  background-color: #f2f2f2;
`

const Title = styled.h3`
  margin: 0 0 1rem 0;
  padding: 0;
  font-size: 1rem;
  background-color: #f2f2f2;
  & a {
    color: #000000;
  }
  & a:hover {
    color: #202c6a;
  }
`

const Excerpt = styled.p`
  margin: 0;
  padding: 0;
  font-size: 0.8rem;
`

const PostMetaList = styled.div`
  float: right;
  margin: 0;
  color: #333;
  font-size: 0.7rem;
  font-weight: light;
  padding: 0.1rem 0.4rem;
  background-color: white;
  border-radius: 0.3rem;
`

const PostPreview = ({ post }) => {
  const timeToRead = `${post.timeToRead} min`
  return (
    <ContentWrapper>
      <PostMetaList>
        <PostMetaItem icon="🗓" iconLabel="Date" text={post.frontmatter.date} />
        {` · `}
        <PostMetaItem icon="⏱" iconLabel="timeToRead" text={timeToRead} />
      </PostMetaList>
      <Title>
        <Link to={post.fields.slug}>{post.frontmatter.title}</Link>{" "}
      </Title>

      <Excerpt>{post.excerpt}</Excerpt>
    </ContentWrapper>
  )
}

export default PostPreview
